/*감정쓰레기통*/
.exiting .letter-popup-content {
  animation: crumple 0.9s forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes crumple {
  0% {
    transform: scale(1);
    opacity: 1;
  }

  100% {
    transform: scale(0.1) rotate(720deg);
    opacity: 0;
  }
}

